import Xiaoxiannan from "./Xiaoxiannan/index";
import tbButton from "./tbButton/index"; //tb-button 
import tbRow from "./tbRow/index"; //tb-row 
import tbCol from "./tbCol/index"; //tb-col 
import tbLink from "./tbLink/index"; //tb-link 
import tbContainer from "./tbContainer/index"; // tb-container
import tbAside from "./tbAside/index"; // tb-aside
import tbMain from "./tbMain/index"; // tb-main
import tbHeader from "./tbHeader/index"; // tb-header
import tbFooter from "./tbFooter/index"; // tb-footer

import tbRadio from "./tbRadio/index"; // tb-radio
import tbRadioButton from "./tbRadioButton/index"; // tb-radio-button
import tbRadioGroup from "./tbRadioGroup/index"; // tb-radio-group

import tbCheckbox from "./tbCheckbox/index"; // tb-checkbox
import tbCheckboxButton from "./tbCheckboxButton/index"; // tb-checkbox-button
import tbCheckboxGroup from "./tbCheckboxGroup/index"; // tb-checkbox-group

import tbTag from "./tbTag/index"; // tag

import tbColor from "./tbColor/index"; // 
import tbColorPicker from "./tbColorPicker/index"; // 

import tbCalendar from "./tbCalendar/index"; // 

import tbPopconfirm from "./tbPopconfirm/index"; // 

import tbDivider from "./tbDivider/index"; // 

import tbBadge from "./tbBadge/index"; // 

import tbAvatar from "./tbAvatar/index"; // 

import tbBacktop from "./tbBacktop/index"; // 

import tbCarousel from "./tbCarousel/index"; // 
import tbCarouselItem from "./tbCarouselItem/index"; // 

import "./iconfont/iconfont.css"; //icon
import "./global.css";
// 
const components = [Xiaoxiannan, tbButton, tbRow, tbCol, tbLink, tbContainer, tbAside, tbMain, tbHeader, tbFooter, tbRadio, tbRadioButton, tbRadioGroup, tbCheckbox, tbCheckboxGroup, tbCheckboxButton, tbTag, tbColor, tbColorPicker, tbCalendar, tbPopconfirm, tbDivider, tbBadge, tbAvatar, tbBacktop, tbCarousel, tbCarouselItem];

//  install 
const install = function(Vue) {
  if (install.installed) return;
  install.installed = true;
  // Vue.radioGroup=""
  Vue.directive("inpClick", {
    //  DOM 
    inserted: function(el) {
      // 
      el.onclick = function() {
        el.getElementsByTagName("input")[0].click();
      };
    },
    unbind(el) {
      // 
      // 
      el.onclick = null;
    },
  });
  // 
  components.map((component) => {
    Vue.component(component.name, component);
  });
};

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export default {
  //  install 
  install,
  // 
  ...components,
};
